		 url('../fonts/eg-icons.woff') format('woff'),
		 url('../fonts/eg-icons.ttf') format('truetype'),
		 url('../fonts/eg-icons.svg#eg-icons') format('svg');
}

.icon {
	position: relative;
	display: inline-block;

	&:before {
		display: inline-block;
		text-decoration: none;
		text-transform: none;
		font: normal 1em/1em 'New Glyphicons Regular';
		font-style: normal;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-moz-user-select: moz-none;
		-ms-user-select: none;
		user-select: none;
		text-rendering: optimizeLegibility;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale; // Will correct font-weight begining in FF 25
	}
}
.icon.eg-icon:before {
	font-family: 'EG Icons';
}
.icon.social:before {
	font-family: 'New Glyphicons Social Regular';
}
.icon.halflings:before {
	font-family: 'New Glyphicons Halflings Regular';
}
.icon.filetype:before {
	font-family: 'New Glyphicons Filetypes Regular';
}

